/* TaskItem Styles */
.task-item {
    padding: 15px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    margin-bottom: 10px;
  }
  
  .task-item h3 {
    font-size: 16px;
    margin: 0;
  }
  
  .task-item p {
    font-size: 14px;
    color: #666;
  }
  
  .task-item small {
    color: #aaa;
  }
  