/* TaskList Styles */
.task-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .task-item {
    display: flex;
    flex-direction: column;
    gap: 10px;
    background-color: #fff;
    border-radius: 8px;
    padding: 15px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .task-item h3 {
    font-size: 18px;
    font-weight: 600;
    margin: 0;
  }
  
  .task-item p {
    font-size: 14px;
    color: #666;
  }
  
  .task-item small {
    font-size: 12px;
    color: #aaa;
  }
  